import React from 'react';
// Imports from @mui/material (use direct default imports)
import Box from '@mui/material/Box';
// import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Grid from '@mui/material/Grid';
import LinearProgress from '@mui/material/LinearProgress';
import Chip from '@mui/material/Chip';
import Button from '@mui/material/Button';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemIcon from '@mui/material/ListItemIcon';
// Add any other MUI components you need here, such as Box, Typography, etc.
// Imports from @mui/icons-material (use direct default imports)
// NOTE: Use the 'as' keyword if you want to keep the Icon suffix for clarity

import People from '@mui/icons-material/People'; // Used in StatCard and Quick Actions
import School from '@mui/icons-material/School'; // Used in StatCard and Quick Actions
import TrendingUp from '@mui/icons-material/TrendingUp'; // Used in StatCard and Quick Actions
import Payment from '@mui/icons-material/Payment'; // Used in StatCard and Quick Actions
import CheckCircle from '@mui/icons-material/CheckCircle'; // Used in Recent Users list
import Schedule from '@mui/icons-material/Schedule'; // Used in Recent Users list
// You may also need MenuIcon if this dashboard is used with the Layout component
// import MenuIcon from '@mui/icons-material/Menu';

import { DataTable, PageHeader, LoadingSpinner, useAuth } from '@eduable/shared-components';
import { apiClient } from '@eduable/shared-utils';
import { useQuery } from '@tanstack/react-query';

const StatCard: React.FC<{
  title: string;
  value: string;
  subtitle: string;
  icon: React.ReactNode;
  color?: string;
  progress?: number;
}> = ({ title, value, subtitle, icon, color = 'primary', progress }) => {
  return (
    <Card sx={{ height: '100%', transition: 'all 0.3s', '&:hover': { transform: 'translateY(-4px)' } }}>
      <CardContent>
        <Box display="flex" alignItems="flex-start" justifyContent="space-between">
          <Box>
            <Typography color="textSecondary" gutterBottom variant="overline">
              {title}
            </Typography>
            <Typography variant="h4" component="div" fontWeight="bold">
              {value}
            </Typography>
            <Typography variant="body2" color="textSecondary">
              {subtitle}
            </Typography>
            {progress !== undefined && (
              <Box sx={{ mt: 2 }}>
                <LinearProgress
                  variant="determinate"
                  value={progress}
                  sx={{ height: 8, borderRadius: 4 }}
                  color={color as any}
                />
              </Box>
            )}
          </Box>
          <Box
            sx={{
              p: 1,
              borderRadius: 2,
              bgcolor: `${color}.light`,
              color: `${color}.contrastText`,
            }}
          >
            {icon}
          </Box>
        </Box>
      </CardContent>
    </Card>
  );
};

const AdminDashboard: React.FC = () => {
  const { user } = useAuth();

  const { data: dashboardData, isLoading } = useQuery({
    queryKey: ['admin-dashboard'],
    queryFn: async () => {
      const [users, courses, analytics, systemHealth] = await Promise.all([
        apiClient.get('/admin/users'),
        apiClient.get('/admin/courses'),
        apiClient.get('/admin/analytics'),
        apiClient.get('/admin/system-health'),
      ]);
      return { users, courses, analytics, systemHealth };
    },
  });

  if (isLoading) {
    return <LoadingSpinner fullScreen message="Loading admin dashboard..." />;
  }

  const users = dashboardData?.users || [];
  const courses = dashboardData?.courses || [];
  const analytics = dashboardData?.analytics || {};
  const systemHealth = dashboardData?.systemHealth || {};

  const activeUsers = users.filter((u: any) => u.isActive);
  const recentUsers = users.slice(0, 5);

  const systemStatusColumns = [
    { field: 'service', headerName: 'Service', flex: 1 },
    { field: 'status', headerName: 'Status', flex: 1, renderCell: (params: any) => (
      <Chip
        label={params.value}
        color={params.value === 'Operational' ? 'success' : 'error'}
        size="small"
      />
    )},
    { field: 'responseTime', headerName: 'Response Time', flex: 1 },
    { field: 'uptime', headerName: 'Uptime', flex: 1 },
  ];

  const systemStatusData = [
    { id: 1, service: 'API Gateway', status: 'Operational', responseTime: '45ms', uptime: '99.9%' },
    { id: 2, service: 'User Service', status: 'Operational', responseTime: '23ms', uptime: '99.8%' },
    { id: 3, service: 'Content Service', status: 'Operational', responseTime: '67ms', uptime: '99.7%' },
    { id: 4, service: 'Database', status: 'Operational', responseTime: '12ms', uptime: '100%' },
    { id: 5, service: 'Redis Cache', status: 'Operational', responseTime: '5ms', uptime: '99.9%' },
  ];

  return (
    <Box>
      <PageHeader
        title={`Admin Dashboard - ${user?.instituteId || 'Institute'}`}
        subtitle="Monitor system performance and manage your institute"
      />

      <Grid container spacing={3}>
        {/* Stats Overview */}
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Total Users"
            value={users.length.toString()}
            subtitle={`${activeUsers.length} active`}
            icon={<People />}
            color="primary"
            progress={(activeUsers.length / users.length) * 100}
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Active Courses"
            value={courses.length.toString()}
            subtitle="This semester"
            icon={<School />}
            color="success"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Performance"
            value={`${analytics.averagePerformance || 0}%`}
            subtitle="Institute average"
            icon={<TrendingUp />}
            color="info"
          />
        </Grid>
        <Grid item xs={12} sm={6} md={3}>
          <StatCard
            title="Revenue"
            value={`${analytics.monthlyRevenue || 0}`}
            subtitle="This month"
            icon={<Payment />}
            color="warning"
          />
        </Grid>

        {/* System Status */}
        <Grid item xs={12} md={8}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom fontWeight="bold">
                System Status
              </Typography>
              <DataTable
                rows={systemStatusData}
                columns={systemStatusColumns}
                pageSize={5}
                checkboxSelection={false}
              />
            </CardContent>
          </Card>
        </Grid>

        {/* Recent Activity */}
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
                <Typography variant="h6" fontWeight="bold">
                  Recent Users
                </Typography>
                <Button
                  variant="outlined"
                  size="small"
                  onClick={() => window.location.href = '/users'}
                >
                  View All
                </Button>
              </Box>
              <List>
                {recentUsers.map((user: any) => (
                  <ListItem
                    key={user.id}
                    sx={{
                      borderBottom: '1px solid',
                      borderColor: 'divider',
                      '&:last-child': { borderBottom: 'none' },
                    }}
                  >
                    <ListItemIcon>
                      {user.isActive ? (
                        <CheckCircle color="success" />
                      ) : (
                        <Schedule color="disabled" />
                      )}
                    </ListItemIcon>
                    <ListItemText
                      primary={user.name}
                      secondary={
                        <Box>
                          <Typography variant="body2" color="textSecondary">
                            {user.email}
                          </Typography>
                          <Chip
                            label={user.role}
                            size="small"
                            variant="outlined"
                            sx={{ mt: 0.5 }}
                          />
                        </Box>
                      }
                    />
                  </ListItem>
                ))}
              </List>
            </CardContent>
          </Card>
        </Grid>

        {/* Quick Actions */}
        <Grid item xs={12}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom fontWeight="bold">
                Quick Actions
              </Typography>
              <Grid container spacing={2}>
                {[
                  {
                    label: 'User Management',
                    description: 'Manage students and teachers',
                    icon: <People />,
                    action: () => window.location.href = '/users',
                    color: 'primary',
                  },
                  {
                    label: 'Course Setup',
                    description: 'Create and manage courses',
                    icon: <School />,
                    action: () => window.location.href = '/courses',
                    color: 'success',
                  },
                  {
                    label: 'Billing & Plans',
                    description: 'Manage subscriptions',
                    icon: <Payment />,
                    action: () => window.location.href = '/billing',
                    color: 'warning',
                  },
                  {
                    label: 'System Settings',
                    description: 'Configure institute settings',
                    icon: <TrendingUp />,
                    action: () => window.location.href = '/settings',
                    color: 'info',
                  },
                ].map((action, index) => (
                  <Grid item xs={12} sm={6} md={3} key={index}>
                    <Card
                      sx={{
                        cursor: 'pointer',
                        transition: 'all 0.3s',
                        '&:hover': {
                          transform: 'translateY(-4px)',
                          boxShadow: 4,
                        },
                      }}
                      onClick={action.action}
                    >
                      <CardContent sx={{ textAlign: 'center' }}>
                        <Box
                          sx={{
                            p: 2,
                            borderRadius: '50%',
                            bgcolor: `${action.color}.light`,
                            color: `${action.color}.contrastText`,
                            display: 'inline-flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            width: 64,
                            height: 64,
                            mb: 2,
                          }}
                        >
                          {action.icon}
                        </Box>
                        <Typography variant="h6" gutterBottom>
                          {action.label}
                        </Typography>
                        <Typography variant="body2" color="textSecondary">
                          {action.description}
                        </Typography>
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
};

export default AdminDashboard;